<template>
      <div class="input-container">
        <input type="text" class="input-text" maxlength="22"
        v-model="info" @change.prevent="sendInfo" required />
      </div>
</template>

<script>
module.exports = {
  props: ['infoupdated'],
  data: function () {
    return {
      info: '',
    }
  }, 
  mounted: function (){
      if(this.infoupdated){
        return this.info = this.infoupdated
      }
    },
  methods: {
    sendInfo: function(){
      this.$emit('updateinfo', this.info)
    }
  }
}
</script>

<style scoped>

* {
	box-sizing: border-box;
}
*:focus {
  outline: none;
  cursor: pointer;
}

input {
  margin: 0;
  padding:0;
  text-align: center;
  background: transparent;
  border: none;
}
.input-container{
  text-align: center;
  display: flex;
  display: -webkit-flex;
  height: 100%;
}
.input-text{
  text-align: center;
  font-size: 17px;
  color: #555555;
  min-width: 100px;
  margin: 0 auto;
}
input[type="text"].form-amount-infoInput::-webkit-input-placeholder { 
  color: #A9AAAA;
}
</style>


